* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  min-width: 60px;
  color: #f2f7f7;
  text-align: center;
  cursor: pointer;
  background-color: #4b9494;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 3px 5px;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.9;
  transition: color, background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  background-color: #18b8b8;
}

.timer {
  color: #000;
  text-align: center;
  gap: 20px;
  margin-top: 20px;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.15;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  font-size: 22px;
  font-weight: 600;
  line-height: 1.25;
}

